import { EmployeeService } from './employee.service';
import { Employee } from './../model/employee.model';
import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '../../../node_modules/@angular/router';

@Component({
  selector: 'app-display-employee',
  templateUrl: './display-employee.component.html',
  styleUrls: ['./display-employee.component.css']
})
export class DisplayEmployeeComponent implements OnInit {
  employeeId: number;
  @Input() emp: Employee;
  @Input() searchTerm: string;
  constructor(private _route: ActivatedRoute, private _router: Router, private _empService: EmployeeService) { }
  ngOnInit() {
    this.employeeId = +this._route.snapshot.paramMap.get('id');
  }
  getNameAndEmail(): string {
    return this.emp.name + ' ' + this.emp.email;
  }
  viewEmpDetails(): void {
    this._router.navigate(['/employee', this.emp.id]);
  }
  editEmployee(): void {
    this._router.navigate(['/edit', this.emp.id]);
  }
  deleteEmployee(): void {
    const deleteFlag = confirm('Are you sure you want to remove the employee "' + this.emp.name + '" ?');
    if (deleteFlag) {
      this._empService.DeleteEmployees(this.emp.id);
    }
  }
}
