import { EmployeeService } from './employees/employee.service';
import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '../../node_modules/@angular/router';
import { Route } from '../../node_modules/@angular/compiler/src/core';

@Injectable({
  providedIn: 'root'
})
export class ListCanActivateGaurdService implements CanActivate {
  constructor(private _route: Router, private _empService: EmployeeService) { }
  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    const IshaveValue = !!this._empService.GetEmployeeById(+route.paramMap.get('id'));
    if (IshaveValue) {
      return true;
    } else {
      this._route.navigate(['notfound']);
      return true;
    }
  }
}
