import { Employee } from './../../model/employee.model';
import { Component, OnInit, ViewChild } from '@angular/core';
import { BsDatepickerConfig } from 'ngx-bootstrap/datepicker';
import { EmployeeService } from './../employee.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgModel } from '../../../../node_modules/@angular/forms';

@Component({
  templateUrl: './create-employee.component.html',
  styleUrls: ['./create-employee.component.css']
})
export class CreateEmployeeComponent implements OnInit {
  @ViewChild('employeeForm')
  public createEmployeeForm: NgModel;
  employee: Employee = {
    id: null,
    name: null,
    gender: null,
    email: null,
    dateOfBirth: null,
    phoneNumber: null,
    PhotoPath: null,
    Department: 'select',
  };

  isValidImage = false;
  datepickerConfig: Partial<BsDatepickerConfig>;
  gender = 'male';
  blShowSaveButton = true;
  empDepartment = [{ Id: 1, Name: 'IT' }, { Id: 2, Name: 'HR' }, { Id: 3, Name: 'Payroll' }];
  constructor(private _router: Router, private _employeeService: EmployeeService, private _actRoute: ActivatedRoute) {
    this.datepickerConfig = Object.assign({},
      {
        containerClass: 'theme-dark-blue',
        showWeekNumbers: false,
        dateInputFormat: 'DD/MM/YYYY'
      });
  }
  photoPreviewToggle(): void {
    this.isValidImage = !this.isValidImage;
  }
  ngOnInit() {
    this._actRoute.paramMap.subscribe(empParameters => {
      const id = +empParameters.get('id');
      if (id === 0) {
        this.employee = {
          id: null,
          name: null,
          gender: null,
          email: null,
          dateOfBirth: null,
          phoneNumber: null,
          PhotoPath: null,
          Department: 'select',
        };
        this.createEmployeeForm.reset();
        this.blShowSaveButton = true;
      } else {
        this.employee = this._employeeService.GetEmployeeById(id);
        this.blShowSaveButton = false;
      }
    });
  }
  saveEmployee(): void {
    if (this.employee.id === null) {
      this._employeeService.SaveEmployee(this.employee);
      this._router.navigate(['list', { id: 0 }]);
    } else {
      this._router.navigate(['list', { id: this.employee.id }]);
    }
  }
}
