import { Event, Router, NavigationStart, NavigationEnd, NavigationCancel } from '@angular/router';
import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  ShowLoading = false;
  constructor(private _route: Router) {
    this._route.events.subscribe((eventList: Event) => {
      if (eventList instanceof NavigationStart) {
        this.ShowLoading = true;
      }
      if (eventList instanceof NavigationEnd || eventList instanceof NavigationCancel) {
        this.ShowLoading = false;
      }
    });
  }
}
