import { Employee } from './../model/employee.model';
import { EmployeeService } from './employee.service';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '../../../node_modules/@angular/router';
import { Observable } from '../../../node_modules/rxjs';
import { Injectable } from '../../../node_modules/@angular/core';
@Injectable()
export class ListEmployeeResolverService implements Resolve<Employee[]> {
    constructor(private _employee: EmployeeService) {}
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Employee[]>{
       return this._employee.GetEmployeeDetails();
    }
}
