import { ListCanActivateGaurdService } from './list-can-activate-gaurd.service';
import { ListEmployeeResolverService } from './employees/list-employees-resolver-service';
import { EmployeeFilterPipe } from './employees/employee-filter.pipe';
// Modules
import { RouterModule, Routes } from '@angular/router';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';

// Components
import { AppComponent } from './app.component';
import { ListEmployeesComponent } from './employees/list-employees.component';
import { CreateEmployeeComponent } from './employees/create-employee/create-employee.component';
import { CustomValidationDirective } from './shared/customValidation.directive';
import { CustomCompareValidationDirective } from './shared/compareValidation.directive';
import { EmployeeService } from './employees/employee.service';
import { DisplayEmployeeComponent } from './employees/display-employee.component';
import { CanDeactivateGuardService } from './employees/create-employee-can-deactivate-guard.service';
import { EmployeeDetailsComponent } from './employees/employee-details.component';
import { ContentNotFoundComponent } from './content-not-found.component';

const appRoutes: Routes = [
  { path: 'list', component: ListEmployeesComponent, resolve: {employeeList: ListEmployeeResolverService} },
  { path: 'employee/:id', component: EmployeeDetailsComponent, canActivate: [ListCanActivateGaurdService] },
  { path: 'edit/:id', component: CreateEmployeeComponent, canDeactivate: [CanDeactivateGuardService] },
  { path: 'notfound', component: ContentNotFoundComponent },
  { path: '', redirectTo: 'list', pathMatch: 'full' }
];

@NgModule({
  declarations: [
    AppComponent,
    ListEmployeesComponent,
    CreateEmployeeComponent,
    CustomValidationDirective,
    CustomCompareValidationDirective,
    DisplayEmployeeComponent,
    EmployeeDetailsComponent,
    EmployeeFilterPipe,
    ContentNotFoundComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    BsDatepickerModule.forRoot(),
    RouterModule.forRoot(appRoutes),
  ],
  providers: [EmployeeService, CanDeactivateGuardService, ListEmployeeResolverService, ListCanActivateGaurdService],
  bootstrap: [AppComponent]
})
export class AppModule { }
